import axios from 'axios';
import React, { useEffect, useState } from 'react';
import './Profile.css'
import { useNavigate } from 'react-router-dom';
import "../API/api"
import { toast } from 'react-toastify';


export default function UpdateClient() {
    const [id, setId] = useState(null)
    const [clientName, setClientName] = useState('');
    const [location, setLocation] = useState('')
    const [status, setStatus] = useState('')

    const navigate = useNavigate();

    const handleUpdateClient = (e) => {
        e.preventDefault();
        axios.put(`${global.API_URL}/smg/client/editClientDetails/${id}`, {
            clientName: clientName,
            location: location,
            status: status
        }, {
            headers: {
                'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
            }
        })
            .then((res) => {
                // console.log("successfully client details updated", res);
                // eslint-disable-next-line
                if (res.data.msg == "client details updated") {
                    navigate("/profilepage/clientform")
                    toast.success("client details updated successfully")
                }

            })
            .catch((err) => {
                console.log("error found", err);
            })
    }

    useEffect(() => {
        setId(localStorage.getItem('CLIENT_Id'));
        setClientName(localStorage.getItem('CLIENT_NAME'));
        setLocation(localStorage.getItem('LOCATION'));
        setStatus(localStorage.getItem('STATUS'));
    }, [])

    return (
        <div>
            <form onSubmit={handleUpdateClient}>
                <h5>Add Client</h5>
                <div className='ProjectField'>
                    <div>
                        <label className='form-label'>Client Name</label>
                        <input type="text" value={clientName} name="clientName" onChange={(e) => setClientName(e.target.value)} className='form-control ' placeholder='Enter client name' />
                    </div>
                    <div>
                        <label className='form-label'>Client Location</label>
                        <input type="text" value={location} name="location" onChange={(e) => setLocation(e.target.value)} className='form-control' placeholder='Enter client location' />
                    </div>
                    <button type='submit' className='btn btn-success  Btn'>Update Client</button>
                    <button type='submit' className='btn btn-warning  Btn'>Reset</button>
                </div>
            </form>
        </div>
    )
}
