import axios from 'axios';
import React from 'react';
import { useState, useEffect } from 'react';
import { Link} from 'react-router-dom';
import "../API/api"

const CandidateList = () => {
    const [data, setData] = useState([])
    const [status, setStatus] = useState('');

   
    const heading = ['Candidate Name', 'Contact No',  'EmailId', 'Applied For', 'Hr_Name', 'Experience', 'Status', 'Resume', 'Interview', 'Edit' ];
 
    useEffect(()=>{
        const GetCandidateData = () => {
            axios.get(`${global.API_URL}/smg/candidate/getAllCandidate/Inprocess`, {
              headers: {
                'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
              }
            })
              .then((res) => {
                console.log("DATA", res.data);
                setData(res.data)
              }).catch((error) => {
                console.log("error", error);
              })
          }

          GetCandidateData()
    }, [])
    
  
  const findDatabystatus = (status)=>{
    axios.get(`${global.API_URL}/smg/candidate/getAllCandidate/${status}`, {
      headers: {
        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    })
      .then((res) => {
        console.log("DATA", res.data);
        setStatus(status)
        setData(res.data)
      }).catch((error) => {
        console.log("error", error);
      })     
  }
  
   
    
  return (    
    <>
            <label className='form-label fw-bold bi bi-search'>  By Status</label>
            <div className='d-flex p-3'>
            <select className='form-select w-25 h-25' name='status' onChange={(e) => findDatabystatus(e.target.value)} value={status}>
              <option disabled>select</option>
              <option value="Inprocess">Inprocess</option>
              <option value="Selected">Selected</option>
              <option value="Rejected">Rejected</option>
            </select>
            {/* <button type='button'  onClick={findDatabystatus} className='btn btn-outline-success Btn ms-2' style={{marginTop:"-2px"}}>Click</button> */}
          </div>
  
    <div id='addCandi'>
      <div className='Border'>
        <table className="table table-striped">
          <thead>
            <tr>
              {
                heading.map((data, index) => (

                  <td key={index} style={{ fontWeight: '600', color: '#252F40' }}>{data}</td>

                ))
              }
            </tr>
          </thead>
          <tbody >
            {
              data.map((data) => {
                return <tr key={data.id}  >
                  <td ><Link to={"/hrProfile/candidateprofile/"+data.id} style={{textDecoration:'none'}}>{data.candidateName}</Link></td>
                  <td >{data.mobileNo}</td>
                  <td >{data.email}</td>
                  <td >{data.appliedFor}</td>
                  <td >{data.candidateHr}</td>
                  <td >{data.totalExp}</td>
                  <td>{data.status}</td>  
                  <td><a  href={data.resume} style={{textDecoration:'none'}}  target="_blank">Download</a></td>
                  <td><Link to={"/hrProfile/candidateinterview/"+data.id} style={{ cursor: 'pointer', textDecoration:'none' }}>Schedule Interview</Link></td>
                  <td ><Link to={"/hrProfile/candidatelist/"+data.id}  className='bi bi-pen-fill' style={{ cursor: 'pointer', color:'black'  }}></Link></td>   
                </tr>
              })
            }
          </tbody>
        </table>
      </div>
    </div>
    </>
  );
}

export default CandidateList;
