import axios from 'axios';
import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import './Profile.css'
import LibraryBooksSharpIcon from '@mui/icons-material/LibraryBooksSharp';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import "../API/api"
import GroupAddIcon from '@mui/icons-material/GroupAdd';

export default function Sidebar2() {
    const [data, setData] = React.useState([]);
    const [employeeImg, setEmployeeImg] = React.useState("");
    const navigate = useNavigate();
    const routeEmployeePage = () => {
        // axios.get(`${global.API_URL}/smg/employee/getEmpId`, {
        //     headers: {
        //         'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
        //     }
        // })
        //     .then((res) => {
        //         // console.log("Empid", res.data);
        //         const EMPLOYEE_ID = res.data;
        //         window.localStorage.setItem("EMPLOYEE_ID", EMPLOYEE_ID)
        //         // window.location.reload(false);
        //     })
        navigate("/profilepage/addemployee")
    }
    const routeBillingPage = () => {
        navigate("/profilepage/billing")
    }

    const routeAssignPage = () => {
        navigate("/profilepage/assignproject")
    }
    const routeAdminAccessPage = () => {
        navigate("/profilepage/employeeRole")
    }

    useEffect(() => {
        setEmployeeImg(window.localStorage.getItem("IMAGE_OF_ADMIN"));
        getDetails()
    }, []);

    const getDetails = (e) => {
        axios.get(`${global.API_URL}/smg/admin/getAdminDetails/${window.localStorage.getItem("ADMIN_ID")}`, {
            headers: {
                'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
            }
        })
            .then((res) => {
                setData((res.data));
            }).catch((error) => {
                console.log("Some error here", error);
            })
    }

    return (
        <div className='View'>
            <div className='complete-sidebar2'>
                    {
                        
                        <div className="sidebar-map">
                            
                           <div key={data.Sidebar2} className='sidebar-text'>
                           <label>
                           <img src={employeeImg} alt="img"  />
                           {data.name} 
                           </label> 
                            <span> [{data.designation}] </span>
                           
                             </div>
                            <div className='Icons'>
                                <span onClick={routeAdminAccessPage}  ><AdminPanelSettingsIcon style={{ fontSize: '25px', marginTop: '-3px', marginRight: '5px' }} />Admin Access</span>
                                <span onClick={routeEmployeePage} ><GroupAddIcon style={{ fontSize: '25px', marginTop: '-3px', marginRight: '5px' }} />Add Employee </span>
                                <span onClick={routeAssignPage}><LibraryBooksSharpIcon style={{ marginRight: '5px' }} />Assigned Project </span>
                                <span onClick={routeBillingPage}  ><AttachMoneyIcon />Billing</span>
                            </div>
                        
                        </div>
                    }
            </div>
        </div>
    )
}
