import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react';
import './Profile.css'
import { useNavigate } from 'react-router-dom';
import "../API/api"
import { toast } from 'react-toastify';

const initialdataProject = {
  projectName: "",
  projectDescription: "",
  status: "",
  client: ''
}

const ProjectForm = () => {
  const refElement = useRef("");
  const [optionList, setOptionList] = useState([]);
  const navigate = useNavigate();
  const [values, setValues] = useState([]);
  const [data, setData] = useState(initialdataProject);
  const [error, setError] = useState(false);
  const [visible, setVisible] = useState(false)

  const heading = ['Project Name', 'Project Description', "ClientName", "Status", 'Edit']

  const {
    projectName,
    projectDescription,
    status,
    client
  } = data;

  const inputHandle = (e) => {

    setData({
      ...data,
      [e.target.name]: [e.target.value]
    })
  }

  const handleProjects = (e) => {
    e.preventDefault();
    if (!status || !client) {
      setError(true);
      return false;
    }
    axios.post(`${global.API_URL}/smg/project/saveProjectDetails`, {
      projectName: `${projectName}`,
      projectDescription: `${projectDescription}`,
      clientName: `${client}`,
      status: `${status}`
    }, {
      headers: {
        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    })
      .then((res) => {
        console.log("details saved into the database", res.data);
        // eslint-disable-next-line
        if (res.data.message == "details was saved into the database.......") {
          navigate("/profilepage/projectform")
          window.location.reload(false);
          toast.success("Project details saved successfully")
        }
        else if (res.data.message == "already present") {
          navigate("/profilepage/projectform")
          toast.warning("Project Already Present")
        }

      })
      .catch((err) => {
        console.log("error found", err);
      })
  }

  const GetProjectDetails = () => {
    axios.get(`${global.API_URL}/smg/project/getAllProjectDetails`, {
      headers: {
        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    }).then((res) => {
      // console.log("Fetch successfully", res.data);
      setValues(res.data)
    }).catch((err) => {
      console.log("some error", err);
    })
  }

  const GetClientDetails = () => {
    axios.get(`${global.API_URL}/smg/client/getAllClientDetails`, {
      headers: {
        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    }).then((res) => {
      // console.log("ListData fetched", res.data);
      setOptionList(res.data)
    }).catch((err) => {
      console.log("some error", err);
    })
  }

  const handleSearch = (e) => {
    e.preventDefault()
    axios.get(`${global.API_URL}/smg/project/ProjectFilter`,
      {
        params: {
          clientName: `${client}`,
          status: `${status}`
        }
        ,
        headers: {
          'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
        }
      }).then((res) => {
        setValues(res.data)
      }).catch((err) => {
        console.log("some error", err);
      })

  }

  useEffect(() => {
    GetProjectDetails();
    GetClientDetails();
  }, [])

  const editProject = (projectId, projectName, projectDes, projectStatus, ClientName) => {
    localStorage.setItem("Id", projectId);
    localStorage.setItem("P_Name", projectName);
    localStorage.setItem("P_Des", projectDes);
    localStorage.setItem("Status", projectStatus);
    localStorage.setItem("Client_Name", ClientName)
    navigate('/profilepage/updateproject');
  }

  const openFilter = () => {
    setVisible(!visible);
  }

  const handleCancel = () => {
    GetProjectDetails()
    setData(initialdataProject)
    setVisible(false)
  }

  return (
    <>
      {visible ? "" : <button className='btn  bi bi-search me-3 searchButton' onClick={openFilter}></button>}

      {visible ?
        (
          <form onSubmit={handleSearch}>
            <div className='d-flex justify-content-around mb-2 mt-2'>
              <label>Select Client <sup>*</sup></label>
              <select className='form-select w-25' value={client} name="client" onChange={inputHandle} >
                <option ></option>
                {
                  optionList.map((list) => {
                    return <option key={list.clientId} >{list.clientName}</option>
                  })
                }
              </select>

              <label className='form-label'>Project Status <sup>*</sup></label>
              <select className='form-select w-25' name="status" value={status} onChange={inputHandle}  >
                <option > </option>
                <option>InProgress</option>
                <option>Completed</option>
                <option>Pending</option>
              </select>

              <button type='submit' className='btn btn-outline-info bi bi-search'  > Search</button>
              <button type='button' className='btn btn-outline-warning' onClick={handleCancel}> cancel</button>
            </div>
          </form>) : (


          <div>
            <form onSubmit={handleProjects}>
              <h5>Add Project</h5>
              <div className='ProjectField'>
                <div>
                  <label className='form-label'>Project Name <sup>*</sup></label>
                  <input type="text" ref={refElement} required value={projectName} name="projectName" onChange={inputHandle} className='form-control ' placeholder='Enter Project name' />
                </div>
                <div>
                  <label className='form-label'>Project Details <sup>*</sup></label>
                  <input type="text" ref={refElement} required value={projectDescription} name="projectDescription" onChange={inputHandle} className='form-control' placeholder='Enter Project details' />
                </div>

                <div className='SOptions'>
                  <label className='form-label'>Project Status <sup>*</sup></label>
                  <select value={status} name="status" className='form-select ' onChange={inputHandle}>
                    <option>select</option>
                    <option>
                      InProgress
                    </option>
                    <option>
                      Completed
                    </option>
                    <option>
                      Pending
                    </option>
                  </select>
                  {error && !status && <p className='errormsg'>Please Select Status</p>}
                </div>

                <div className='SOptions'>
                  <label>Select Client</label>
                  <select className='form-select mt-2' value={client} name="client" onChange={inputHandle}>
                    <option>Select</option>
                    {
                      optionList.map((list) => {
                        return <option key={list.clientId} >{list.clientName}</option>
                      })
                    }
                  </select>
                  {error && !client && <p className='errormsg'>Please Select Client</p>}
                </div>

                <button type='submit' className='btn btn-success  Btn'>Add Project</button>

              </div>
            </form >
          </div >)}
      <hr />
      <h5 style={{ marginTop: "20px" }}>Project Details</h5>
      <div className='Bord'>
        <table className="table table-striped">
          <thead>
            <tr>
              {
                heading.map((data, index) => (

                  <td key={index} style={{ fontWeight: '600', color: '#252F40' }}>{data}</td>

                ))
              }
            </tr>

          </thead>
          <tbody className='TBody'>
            {
              values.map((value, index) => {
                return <tr key={index}  >
                  <td style={{ color: '#2562AB', cursor: 'pointer' }} >{value.projectName}</td>
                  <td >{value.projectDes}</td>
                  <td >{value.ClientName}</td>
                  <td >{value.projectStatus}</td>
                  <td className='bi bi-pen-fill' style={{ cursor: 'pointer' }} onClick={() => editProject(value.projectId, value.projectName, value.projectDes, value.projectStatus, value.ClientName)}></td>
                </tr>
              })
            }
          </tbody>
        </table>
      </div>
    </>
  );
}

export default ProjectForm;
