import axios from 'axios';
import { useEffect } from 'react';
import React, { useState } from 'react';
import './Profile.css'
import { useNavigate } from 'react-router-dom';
import { Dialog } from '@reach/dialog';
import { Tabs, TabList, Tab, TabPanels, TabPanel } from '@reach/tabs';
import '@reach/dialog/styles.css';
import '@reach/tabs/styles.css';
import CloseIcon from "@mui/icons-material/Close";
import { IconButton } from "@mui/material";
import "../API/api"
import moment from 'moment';
import { toast } from 'react-toastify';

const EmplooyeeList = () => {
  const [data, setData] = useState([]);
  const [successUp, setSuccessUp] = React.useState(false);
  const [roles, setRoles] = useState([])
  const [employeeImg, setEmployeeImg] = React.useState("");
  const navigate = useNavigate();

  const [name, setName] = useState("");
  const [role, setRole] = useState('')
  const [location, setLocation] = useState('')

  const [visible, setVisible] = useState(false)

  useEffect(() => {
    GetEmployeeData()
    allRoles();
  }, []);

  const allRoles = () => {
    axios.get(`${global.API_URL}/smg/candidate/getAllrole`, {
      headers: {

        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    }).then((response) => {
      setRoles(response.data)
    }).catch((error) => {
      console.log("error while fetching data", error)
    })
  }

  const GetEmployeeData = () => {
    axios.get(`${global.API_URL}/smg/employee/getAllEmployeeList`, {
      headers: {
        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    })
      .then((res) => {
        setData(res.data)
      }).catch((error) => {
        console.log("error", error);
      })
  };


  const onDelete = (employeeDetailsId) => {
    axios.post(`${global.API_URL}/smg/employee/deleteEmployee/${employeeDetailsId}`, {}, {
      headers: {
        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    })
      .then(() => {
        toast.warning("Deleted");
        GetEmployeeData();
      }).catch((err) => {
        console.log("error", err);
      })
  }

  const hanldeSearch = () => {
    axios.get(`${global.API_URL}/smg/employee/getemplyeeFilter?fullname=${name}&role=${role}&currentlocation=${location}`, {
      headers: {
        'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
      }
    })
      .then((res) => {
        setData(res.data)
      })
      .catch((err) => {
        console.log(err);
      })

  }

  const Edit = (data) => {
    let {
      employeeDetailsId,
      ctc,
      currentLocation,
      degination,
      role,
      dateOfBirth,
      dateOfJoining,
      dateOfMarriage,
      lastWorkingDay,
      email,
      empId,
      empType,
      fullName,
      gender,
      mobileNo,
      primaryLocation,
      primarySkills,
      qualification,
      secondrySkills,
      status,
      fixedCtc,
      variablePay,
      exp
    } = data;

    window.localStorage.setItem("CTC", ctc);
    window.localStorage.setItem("CRNTLOCATION", currentLocation);
    window.localStorage.setItem("DEGINATION", degination);
    window.localStorage.setItem("ROLE", role);
    window.localStorage.setItem("DOB", dateOfBirth);
    window.localStorage.setItem("DOJ", dateOfJoining);
    window.localStorage.setItem("DOM", dateOfMarriage);
    window.localStorage.setItem("LWD", lastWorkingDay);
    window.localStorage.setItem("EMAIL", email);
    window.localStorage.setItem("EMP_ID", empId);
    window.localStorage.setItem("EMP_TYPE", empType);
    window.localStorage.setItem("FULLNAME", fullName);
    window.localStorage.setItem("GENDER", gender);
    window.localStorage.setItem("MOB_NO", mobileNo);
    window.localStorage.setItem("P_LOC", primaryLocation);
    window.localStorage.setItem("P_SKILLS", primarySkills);
    window.localStorage.setItem("QUALIFICATION", qualification);
    window.localStorage.setItem("S_SKILLS", secondrySkills);
    window.localStorage.setItem("STATUS", status);
    window.localStorage.setItem("EDIT_ID", employeeDetailsId);
    window.localStorage.setItem("FIXED_CTC", fixedCtc);
    window.localStorage.setItem("VARIABLE_PAY", variablePay);
    window.localStorage.setItem("EXPERIENCE", exp);

    const GetEmployeeDocs = () => {
      axios.get(`${global.API_URL}/smg/employee/getImage/${employeeDetailsId}`, {
        headers: {
          'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
        }
      })
        .then((res) => {
          let {
            documentationId,
            certificate10th,
            certificate12th,
            graduation,
            postGraduation,
            paySlip,
            offerLetter1,
            offerLetter2,
            offerLetter3,
            employeeImage,
            resume
          } = res.data;

          window.localStorage.setItem("EMPLOYEEIMAGE", employeeImage)
          window.localStorage.setItem("DOCUMENTATION_ID", documentationId)
          window.localStorage.setItem("CERTIFICATE10TH", certificate10th)
          window.localStorage.setItem("CERTIFICATE12TH", certificate12th)
          window.localStorage.setItem("GRADUATION", graduation)
          window.localStorage.setItem("POSTGRADUATION", postGraduation)
          window.localStorage.setItem("PAYSLIP", paySlip)
          window.localStorage.setItem("OFFERLETTER1", offerLetter1)
          window.localStorage.setItem("OFFERLETTER2", offerLetter2)
          window.localStorage.setItem("OFFERLETTER3", offerLetter3)
          window.localStorage.setItem("RESUME", resume)
        }).catch((error) => {
          console.log("error", error);
        })
    };
    GetEmployeeDocs();

    navigate("/profilepage/editemployee");

  }
  const heading = ['Employee Name', 'EmpId', 'EmailId', 'DOJ', 'Designation', 'Role', 'Work Location', 'Phone No'];

  const handleSuccessUp = (data) => {
    let {
      employeeDetailsId,
      ctc,
      currentLocation,
      degination,
      dateOfBirth,
      dateOfJoining,
      dateOfMarriage,
      lastWorkingDay,
      email,
      empId,
      empType,
      fullName,
      gender,
      mobileNo,
      primaryLocation,
      primarySkills,
      qualification,
      secondrySkills,
      status,
      fixedCtc,
      variablePay,
      exp
    } = data;

    window.localStorage.setItem("CTC", ctc);
    window.localStorage.setItem("CRNTLOCATION", currentLocation);
    window.localStorage.setItem("DEGINATION", degination);
    window.localStorage.setItem("DOB", dateOfBirth);
    window.localStorage.setItem("DOJ", dateOfJoining);
    window.localStorage.setItem("DOM", dateOfMarriage);
    window.localStorage.setItem("LWD", lastWorkingDay);
    window.localStorage.setItem("EMAIL", email);
    window.localStorage.setItem("EMP_ID", empId);
    window.localStorage.setItem("EMP_TYPE", empType);
    window.localStorage.setItem("FULLNAME", fullName);
    window.localStorage.setItem("GENDER", gender);
    window.localStorage.setItem("MOB_NO", mobileNo);
    window.localStorage.setItem("P_LOC", primaryLocation);
    window.localStorage.setItem("P_SKILLS", primarySkills);
    window.localStorage.setItem("QUALIFICATION", qualification);
    window.localStorage.setItem("S_SKILLS", secondrySkills);
    window.localStorage.setItem("STATUS", status);
    window.localStorage.setItem("EDIT_ID", employeeDetailsId);
    window.localStorage.setItem("FIXED_CTC", fixedCtc);
    window.localStorage.setItem("VARIABLE_PAY", variablePay);
    window.localStorage.setItem("EXPERIENCE", exp);


    const GetEmployeeDocs = () => {
      axios.get(`${global.API_URL}/smg/employee/getImage/${employeeDetailsId}`, {
        headers: {
          'Authorization': `Bearer ${JSON.parse(window.localStorage.getItem("TOKEN"))}`
        }
      })
        .then((res) => {
          let {
            documentationId,
            certificate10th,
            certificate12th,
            graduation,
            postGraduation,
            paySlip,
            offerLetter1,
            offerLetter2,
            offerLetter3,
            employeeImage,
            resume
          } = res.data;

          window.localStorage.setItem("EMPLOYEEIMAGE", employeeImage)
          window.localStorage.setItem("DOCUMENTATION_ID", documentationId)
          window.localStorage.setItem("CERTIFICATE10TH", certificate10th)
          window.localStorage.setItem("CERTIFICATE12TH", certificate12th)
          window.localStorage.setItem("GRADUATION", graduation)
          window.localStorage.setItem("POSTGRADUATION", postGraduation)
          window.localStorage.setItem("PAYSLIP", paySlip)
          window.localStorage.setItem("OFFERLETTER1", offerLetter1)
          window.localStorage.setItem("OFFERLETTER2", offerLetter2)
          window.localStorage.setItem("OFFERLETTER3", offerLetter3)
          window.localStorage.setItem("RESUME", resume)
          setEmployeeImg(res.data.employeeImage)
          setSuccessUp(true);
        }).catch((error) => {
          console.log("error", error);
        })
    };
    GetEmployeeDocs();

  }
  const handleSuccessClose = () => {
    setSuccessUp(false);
  }

  const openFilter = () => {
    setVisible(!visible);
  }

  const hanldeClear = () => {
    setRole("")
    setName("")
    setLocation("")
    GetEmployeeData()
    setVisible(false)
  }

  return (
    <>
      {visible ? "" : <button className='btn bi bi-search me-3 searchButton' onClick={openFilter}></button>}


      <div style={{ padding: '5px' }}>
        <a href='http://13.232.73.1:8080/smg/download/employee-csv' target="_blank"><button className='btn btn-outline-success'> Download Data</button></a>
      </div>

      {visible &&

        <div className='d-flex justify-content-around mb-2 mt-2'>
          <input className='form-control w-25' placeholder='Enter Employee Name' name='name' value={name} onChange={(e) => setName(e.target.value)} />
          <select className='form-select w-25' placeholder='Enter Employee Name' name='role' value={role} onChange={(e) => setRole(e.target.value)} >
            <option ></option>
            {
              roles.map((list, index) => {
                return <option key={index}>{list}</option>
              })
            }
          </select>
          <select className='form-select w-25' name='location' onChange={(e) => setLocation(e.target.value)}>
            <option ></option>
            <option >Bangalore</option>
            <option >Hyderabad</option>
            <option >Tirupati</option>
          </select>
          <button type='submit' className='btn btn-outline-info bi bi-search' onClick={hanldeSearch}> Search</button>
          <button type='button' className='btn btn-outline-danger' onClick={hanldeClear}> Clear</button>

        </div>


      }
      <Dialog isOpen={successUp} onDismiss={handleSuccessClose} className="dialogbox" >
        <div className='close-button'>
          <IconButton onClick={handleSuccessClose}>
            <CloseIcon />
          </IconButton>
        </div>

        <Tabs>
          <TabList className="tabsnav">
            <Tab className="tabH1">Employee Details</Tab>
            <Tab className="tabH2">Documets</Tab>
          </TabList>
          <TabPanels className="contentDetails">
            <TabPanel>
              <div>

                <div className='HeadingImage'>
                  <img src={employeeImg} alt="" />
                </div>
              </div>
              <form className='PopUpDetails'>
                <div className='empList row'>
                  <span className='col col-lg-6'>Employee Id :</span ><span className='col col-lg-6' style={{ color: '#000000' }}> {window.localStorage.getItem("EMP_ID")}</span>
                  <span className='col col-lg-6'>Full Name :</span><span className='col col-lg-6' style={{ color: '#000000' }}>{localStorage.getItem("FULLNAME")} </span>
                  <span className='col col-lg-6'>Designation : </span><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("DEGINATION")}</span>
                  <span className='col col-lg-6'>Gender : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("GENDER")}</span>
                  <span className='col col-lg-6'>Mobile No : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("MOB_NO")}</span>
                  <span className='col col-lg-6'>Email : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("EMAIL")}</span>
                  <span className='col col-lg-6'>Qualification : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("QUALIFICATION")}</span>
                  <span className='col col-lg-6'>DOB :</span><span className='col col-lg-6' style={{ color: '#000000' }}> {moment(data.dateOfBirth).utc().format('MMM Do, YYYY')}  </span>
                  <span className='col col-lg-6'>DOJ :</span><span className='col col-lg-6' style={{ color: '#000000' }}> {moment(data.dateOfJoining).utc().format('MMM Do, YYYY')} </span>
                  <span className='col col-lg-6'>Current Location :</span> <span className='col-6' style={{ color: '#000000' }}>{window.localStorage.getItem("CRNTLOCATION")}</span>
                  <span className='col col-lg-6'>Primary Location :</span ><span className='col col-lg-6' style={{ color: '#000000' }}> {window.localStorage.getItem("P_LOC")}</span>
                  <span className='col col-lg-6'>Primary Skill : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("P_SKILLS")}</span>
                  <span className='col col-lg-6'>Secondary Skill : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("S_SKILLS")}</span>
                  <span className='col col-lg-6'>Fixed CTC : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("FIXED_CTC")}</span>
                  <span className='col col-lg-6'>Variable Pay : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("VARIABLE_PAY")}</span>
                  <span className='col col-lg-6'>Experience : </span ><span className='col col-lg-6' style={{ color: '#000000' }}>{window.localStorage.getItem("EXPERIENCE")}</span>
                </div>
              </form>
            </TabPanel>
            <TabPanel>
              <div className='PopUpDetails'>
                <div className='empList row'>
                  {/* <button onClick={window.location.reload(false)}> Show Docs </button> */}
                  {/* <span className='col col-lg-6'>DocumentationId :</span> <span className='col col-lg-6' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("DOCUMENTATION_ID")} target="_blank"></a>Id</span> */}
                  <span className='col col-lg-7'>EmployeeImage :</span> <span className='col col-lg-5' style={{ color: '#000000' }}><a href={window.localStorage.getItem("EMPLOYEEIMAGE")} target="_blank" > {(window.localStorage.getItem("EMPLOYEEIMAGE") == "null") ? "" : <h6> Download </h6>}  </a> </span>
                  <span className='col col-lg-7'>Certificate10th :</span> <span className='col col-lg-5' style={{ color: '#000000' }}><a href={window.localStorage.getItem("CERTIFICATE10TH")} target="_blank">{(window.localStorage.getItem("CERTIFICATE10TH") == "null") ? "" : <h6> Download </h6>}</a> </span>
                  <span className='col col-lg-7'>Certificate12th :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("CERTIFICATE12TH")} target="_blank"> {(window.localStorage.getItem("CERTIFICATE12TH") == "null") ? "" : <h6>Download</h6>} </a> </span>
                  <span className='col col-lg-7'>Graduation :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("GRADUATION")} target="_blank">{(window.localStorage.getItem("GRADUATION") == "null") ? "" : <h6> Download </h6>}</a></span>
                  <span className='col col-lg-7'>PostGraduation :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("POSTGRADUATION")} target="_blank">{(window.localStorage.getItem("POSTGRADUATION") == "null") ? "" : <h6> Download </h6>}</a></span>
                  <span className='col col-lg-7'>PaySlip :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("PAYSLIP")} target="_blank">{(window.localStorage.getItem("PAYSLIP") == "null") ? "" : <h6> Download </h6>}</a></span>
                  <span className='col col-lg-7'>OfferLetter1 :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("OFFERLETTER1")} target="_blank">{(window.localStorage.getItem("OFFERLETTER1") == "null") ? "" : <h6> Download </h6>}</a></span>
                  <span span className='col col-lg-7'>OfferLetter2 :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("OFFERLETTER2")} target="_blank">{(window.localStorage.getItem("OFFERLETTER2") == "null") ? "" : <h6> Download </h6>}</a></span>
                  <span className='col col-lg-7'>OfferLetter3 :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("OFFERLETTER3")} target="_blank">{(window.localStorage.getItem("OFFERLETTER3") == "null") ? "" : <h6> Download </h6>}</a></span>
                  <span className='col col-lg-7'>Resume :</span> <span className='col col-lg-5' style={{ color: '#000000' }}> <a href={window.localStorage.getItem("RESUME")} target="_blank"> {(window.localStorage.getItem("RESUME") == "null") ? "" : <h6> Download </h6>} </a></span>
                </div>
              </div>
            </TabPanel>
          </TabPanels>
        </Tabs>
      </Dialog>

      <div className='Border'>
        <table className="table table-striped">
          <thead>
            <tr>
              {
                heading.map((data, index) => (

                  <th key={index} style={{ fontWeight: '600', color: '#252F40' }}>{data}</th>

                ))
              }
            </tr>

          </thead>
          <tbody  >
            {
              data.map((data, index) => {
                // console.log("dobByLS", window.localStorage.getItem("DOB"));
                // console.log("dobByDirect", data.dateOfBirth);
                // console.log("dobByLSM", moment((window.localStorage.getItem("DOB"))).utc().format('MMM Do, YYYY'));
                // console.log("dobByDirectM", moment(data.dateOfBirth).utc().format('MMM Do, YYYY'));

                return <tr key={index}  >
                  <td style={{ color: '#2562AB', cursor: 'pointer' }} onClick={() => handleSuccessUp(data)}>{data.fullName}</td>
                  <td >{data.empId}</td>
                  <td >{data.email}</td>
                  <td >{moment(data.dateOfJoining).utc().format('MMM Do, YYYY')}</td>
                  <td >{data.degination}</td>
                  <td >{data.role}</td>
                  <td >{data.primaryLocation}</td>
                  <td >{data.mobileNo}</td>
                  <td onClick={() => Edit(data)} className='bi bi-pen-fill' style={{ cursor: 'pointer' }}></td>
                  <td className='bi bi-trash3-fill' style={{ color: 'red', cursor: 'pointer' }} onClick={() => onDelete(data.employeeDetailsId)}></td>
                </tr>
              })
            }
          </tbody>
        </table>
      </div>
    </>
  );
}

export default EmplooyeeList;
